"use scrict";
import { HOME } from "@/config/router.js";
import MyForm from "@/components/MyForm/index.vue";
import MyFormItem from "@/components/MyFormItem/index.vue";
import MySelect from "@/components/MySelect/index.vue";
import cityData from '@/common/city.data.js';
import { toast } from "@/config/package.js";
import { isEmpty } from "@/utils/util.js";
import { mapActions } from "vuex";

export default {
	data() {
		return {
			region: "",
			detail: {
				role: "",
				schoolId: "",
				no: "",
				pwd: ""
			},
			schoolOptions: undefined,
			cityOptions: cityData,
			rules: {
				role: "角色不能为空",
				schoolId: "学校不能为空",
				no: (data, callback) => {
					isEmpty(data) && callback(`${this.detail.role === "TEACHER" ? "教师号" : "学生号"}不能为空`);
				},
				pwd: "密码不能为空"
			}
		}
	},
	watch: {
		region(value) {
			value && this.loadSchoolOptions();
		}
	},
	onLoad({label,value}) {
		uni.setNavigationBarTitle({
			title: `绑定${label}`
		});
		this.detail.roleLabel = label;
		this.detail.role = value;
	},
	methods: {
		...mapActions("user", ["loadUserInfo"]),
		linkToBind: function () {
			uni.navigateBack({
				delta: 1
			});
		},
		loadSchoolOptions: function () {
			this.$post(this.$api.GET_SCHOOL_LIST, {
				distinctCode: this.region[this.region.length-1].label
			}, {showLoading: false}).then(res => {
				this.schoolOptions = res.data;
			});
		},
		regionFormatLabel: function (value) {
			return value && value.map(item => item.value).join("/");
		},
		regionFormatValue: function (value) {
			return value;
		},
		submit: async function () {
			let res = await this.$post(this.$api.BIND_ROLE, this.detail);
			await toast({title: "绑定成功" ,icon: "success"});
			await this.loadUserInfo();
			uni.switchTab({
				url: HOME
			});
		}
	},
	components: {
		MyForm,
		MyFormItem,
		MySelect
	}
}
